/* Colors */
@font-face {
  font-family: "Cuprum";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Cuprum"), url("../fonts/cuprum.woff2") format("woff2"), url("../fonts/cuprum.woff") format("woff");
}

@font-face {
  font-family: "Cuprum";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Cuprum-Bold"), url("../fonts/cuprumbold.woff2") format("woff2"), url("../fonts/cuprumbold.woff") format("woff");
}

@font-face {
  font-family: "Cuprum";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: local("Cuprum-Italic"), url("../fonts/cuprumitalic.woff2") format("woff2"), url("../fonts/cuprumitalic.woff") format("woff");
}

@font-face {
  font-family: "Cuprum";
  font-style: italic;
  font-weight: 700;
  font-display: swap;
  src: local("Cuprum-BoldItalic"), url("../fonts/cuprumbolditalic.woff2") format("woff2"), url("../fonts/cuprumbolditalic.woff") format("woff");
}

@font-face {
  font-family: "PTSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PTSans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}

@font-face {
  font-family: "PTSans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Cuprum", "Arial", sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 960px;
  padding: 0 10px;
  margin: 0 auto;
}

.button {
  display: block;
  padding: 10px;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #ee3643;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.button:hover {
  background-color: #ca2c37;
}

.button:active {
  background-color: #ba2732;
}

.page-header {
  padding-bottom: 65px;
  background-color: #f4f7f9;
}

.page-header__wrapper-top {
  background-color: #293449;
}

.page-header__wrapper-top > .container {
  display: flex;
}

.page-header__wrapper-mid {
  padding: 46px 10px 0 63px;
  margin-bottom: 39px;
  display: flex;
  align-items: flex-start;
}

.page-header__page-title {
  margin: 3px 67px 0 0;
  flex-shrink: 0;
}

.page-header__contacts {
  margin-right: auto;
  flex-shrink: 0;
}

.logo {
  display: block;
  width: 220px;
  padding-top: 11px;
  padding-bottom: 7px;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #ee3643;
  box-shadow: 0 5px 0 0 #b52933;
}

.logo[href]:hover {
  background-color: #ca2c37;
  box-shadow: 0 5px 0 0 #9a212a;
}

.logo[href]:active {
  background-color: #ba2732;
  box-shadow: 0 5px 0 0 #8e1e26;
}

.logo--footer {
  padding: 17px 0;
  font-size: 30px;
  line-height: 30px;
}

.search-form {
  position: relative;
  width: 270px;
}

.search-form:hover {
  background-color: #212a3a;
}

.search-form:hover .search-form__icon {
  opacity: 1;
}

.search-form:active {
  background-color: transparent;
}

.search-form:active .search-form__icon {
  opacity: 0.5;
}

.search-form__icon {
  position: absolute;
  top: 12px;
  left: 17px;
  fill: #ffffff;
  opacity: 0.3;
  cursor: pointer;
}

.search-form__input {
  width: 100%;
  padding: 12px 15px 12px 48px;
  font-size: 17px;
  line-height: 18px;
  color: #ffffff;
  background-color: transparent;
  border: none;
}

.search-form__input::placeholder {
  font-size: 17px;
  line-height: 18px;
  color: #ffffff;
}

.search-form__input:focus {
  color: #000000;
  background-color: #ffffff;
  outline: none;
}

.search-form__input:focus ~ .search-form__icon {
  fill: #ee3643;
  opacity: 1;
}

.head-btn {
  position: relative;
  display: block;
  width: 150px;
  padding: 12px 0 12px 17px;
  font-size: 17px;
  line-height: 18px;
  text-align: center;
  color: #ffffff;
}

.head-btn:hover {
  background-color: #212a3a;
}

.head-btn:hover .head-btn__icon--bookmarks,
.head-btn:hover .head-btn__icon--cart {
  opacity: 1;
}

.head-btn:active {
  background-color: #161d29;
}

.head-btn:active .head-btn__icon--bookmarks,
.head-btn:active .head-btn__icon--cart {
  opacity: 0.5;
}

.head-btn__icon {
  position: absolute;
  top: 12px;
  left: 17px;
}

.head-btn__icon--bookmarks {
  fill: #ffffff;
  opacity: 0.3;
}

.head-btn__icon--cart {
  fill: #ffffff;
  opacity: 0.3;
}

.head-btn--order-btn {
  padding-left: 0;
  background-color: #63a63e;
}

.head-btn--order-btn:hover {
  background-color: #5fbb2d;
}

.head-btn--order-btn:active {
  background-color: #518534;
}

.head-btn--full {
  background-color: #ee3643;
}

.head-btn--full:hover {
  background-color: #ca2c37;
}

.head-btn--full:active {
  background-color: #ba2732;
}

.page-title {
  width: 200px;
  font-size: 16px;
  font-style: italic;
  font-weight: normal;
  line-height: 23px;
  color: #ee3643;
}

.contacts {
  width: 270px;
}

.contacts__telephone {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color: #ffffff;
}

.contacts__telephone--block {
  position: relative;
  display: block;
  width: 100%;
  padding: 9px 0 9px 73px;
  margin-bottom: 4px;
  font-size: 21px;
  line-height: 21px;
  text-transform: uppercase;
  color: #ee3643;
  border: 3px solid #ffffff;
}

.contacts__telephone--block:hover .contacts__icon {
  opacity: 0.6;
}

.contacts__telephone--block:active .contacts__icon {
  opacity: 0.3;
}

.contacts__icon {
  position: absolute;
  top: 10px;
  left: 13px;
}

.contacts__address {
  font-size: 14px;
  line-height: 24px;
  font-style: normal;
  text-align: center;
  color: #32425c;
}

.user-navigation {
  display: flex;
  flex-wrap: wrap;
  width: 300px;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 21px;
  line-height: 21px;
}

.user-navigation__item {
  padding: 0;
  margin: 0;
}

.user-navigation__link {
  position: relative;
  display: block;
  padding: 12px 24px 12px;
  color: #000000;
  background-color: #ffffff;
}

.user-navigation__link:hover {
  color: #ee3643;
}

.user-navigation__link:hover .user-navigation__icon {
  opacity: 1;
}

.user-navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}

.user-navigation__link:active .user-navigation__icon {
  opacity: 0.5;
}

.user-navigation__link--login {
  margin-left: 19px;
  margin-right: 10px;
  padding-left: 48px;
}

.user-navigation__link--logout {
  height: 100%;
  padding: 0 22px;
}

.user-navigation__icon {
  position: absolute;
  top: 50%;
  left: 14px;
  transform: translateY(-50%);
  opacity: 0.3;
}

.user-navigation__link--profile {
  width: 256px;
  padding-left: 47px;
  padding-right: 0;
}

.user-navigation__link--secondary {
  padding: 0 17px;
  margin: 8px 0 0;
  font-size: 16px;
  line-height: 18px;
  text-decoration: underline;
  color: #32425c;
  background-color: transparent;
}

.user-navigation__link--secondary:active {
  color: rgba(50, 66, 92, 0.3);
}

.user-navigation__link--dot::before {
  position: absolute;
  left: 100%;
  top: 7px;
  width: 4px;
  height: 4px;
  content: "";
  background-color: #32425c;
  border-radius: 50%;
}

.site-navigation__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-navigation__item {
  flex-grow: 1;
}

.site-navigation__item:first-child > .site-navigation__link {
  padding-left: 47px;
}

.site-navigation__item:last-child > .site-navigation__link {
  padding-right: 49px;
}

.site-navigation__link {
  display: block;
  padding: 20px 25px 18px;
  font-size: 17px;
  line-height: 17px;
  text-align: center;
  color: #ffffff;
  background-color: #32425c;
  box-shadow: 0 5px 0 0 #293449;
}

.site-navigation__link:hover {
  background-color: #293449;
}

.site-navigation__link:active {
  color: rgba(255, 255, 255, 0.5);
  background-color: #1d2739;
  box-shadow: 0 5px 0 0 #1d2739;
}

.site-navigation__link--product {
  color: #ffd180;
}

.site-navigation__link--product:active {
  color: rgba(255, 209, 128, 0.5);
}

.promo-categories {
  padding-top: 59px;
  margin-bottom: 53px;
}

.promo-categories__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 0 -286px 0;
  padding: 0;
  list-style: none;
}

.promo {
  position: relative;
  width: 300px;
  min-height: 123px;
  margin-bottom: 20px;
  padding: 21px 0 0 23px;
}

.promo__title {
  margin: 0 0 12px 0;
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
}

.promo__btn {
  width: 135px;
  background-color: rgba(0, 0, 0, 0.1);
}

.promo__btn:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

.promo__btn:active {
  background-color: rgba(0, 0, 0, 0.3);
}

.promo--materials {
  background: #ffbf47 url("../img/promo-1.svg") no-repeat 213px 31px;
}

.promo--tools {
  background: #3bbce3 url("../img/promo-2.svg") no-repeat 197px 33px;
}

.promo--tech {
  background: #dc91d8 url("../img/promo-3.svg") no-repeat 191px 31px;
}

.promo--sale {
  background: #8ed78f url("../img/promo-4.svg") no-repeat 196px 26px;
}

.promo--delivery {
  background: #ffbf47 url("../img/promo-5.svg") no-repeat 191px 32px;
}

.promo--offset {
  margin-left: 640px;
}

.new::before {
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 400;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  content: "new";
  width: 60px;
  height: 60px;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: -1px;
  text-align: center;
  color: #ffffff;
  transform: rotate(45deg);
}

.new::after {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  content: "";
  width: 60px;
  height: 60px;
  background-image: linear-gradient(45deg, transparent 50%, #ee3643 50%, #ee3643 82%, transparent 82%);
}

.slider {
  position: relative;
  width: 620px;
  height: 266px;
  background-color: #333333;
}

.slider--nojs .slider__controls {
  display: none;
}

.slider__control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  width: 22px;
  height: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.slider__control--prev {
  left: 25px;
  background: url("../img/left-icon.svg");
}

.slider__control--next {
  right: 20px;
  background: url("../img/right-icon.svg");
}

.slider__toggle {
  position: absolute;
  z-index: 3;
  left: 50%;
  bottom: 32px;
  transform: translateX(-50%);
}

.slider__dot {
  position: relative;
  padding: 0;
  margin-right: 6px;
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.slider__dot--active::before {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 6px;
  height: 6px;
  content: "";
  background-color: #ee3643;
  border-radius: 50%;
}

.slider__slide-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.slider__slide {
  display: none;
}

.slide {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 24px;
}

.slide--drill {
  background: url("../img/slide-2.jpg") no-repeat 0 0;
}

.slide--puncher {
  background: url("../img/slide-1.jpg") no-repeat 0 0;
}

.slide--show {
  display: block;
}

.slide__heading {
  margin-bottom: 122px;
  color: #ffffff;
}

.slide__title {
  margin: 0;
  font-size: 36px;
  line-height: 36px;
  font-weight: bold;
  text-transform: uppercase;
}

.slide__slogan {
  font-size: 18px;
  line-height: 24px;
  font-weight: normal;
}

.slide__catalog {
  width: 195px;
}

.section-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 89px;
  padding: 0 24px 0 30px;
  background-color: #f7f3ec;
}

.section-heading__title {
  max-width: 600px;
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: 400;
  color: #32425c;
}

.section-heading__btn {
  width: 253px;
}

.popular-goods {
  margin-bottom: 20px;
}

.popular-goods__heading {
  margin-bottom: 20px;
}

.popular-goods__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px -20px;
  padding: 0;
  list-style: none;
}

.popular-goods__item {
  position: relative;
  margin: 0 10px 20px;
}

.product {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 220px;
  padding-bottom: 20px;
  text-align: center;
  background-color: #ffffff;
  border: 1px solid #eaeaea;
}

.product:hover {
  box-shadow: 0 10px 25px 0 rgba(41, 52, 73, 0.5);
  transition: all 0.2s;
}

.product:hover .product__hover {
  z-index: 1;
}

.product__wrapper {
  position: relative;
  display: flex;
  width: 100%;
  height: 178px;
}

.product__image {
  margin: auto;
}

.product__hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 170px;
  padding-top: 44px;
  background-color: #ffffff;
}

.product__hover--show {
  z-index: 1;
}

.product__buy-btn {
  position: relative;
  margin-bottom: 7px;
  width: 135px;
  background-color: #63a63e;
  box-shadow: inset 0 -3px 0 0 #367315;
}

.product__buy-btn:hover {
  background-color: #5fbb2d;
}

.product__buy-btn:hover .product__cart-icon {
  opacity: 1;
}

.product__buy-btn:active {
  background-color: #518534;
  box-shadow: inset 0 -3px 0 0 #518534;
}

.product__cart-icon {
  position: absolute;
  top: 11px;
  left: 12px;
  fill: #ffffff;
  opacity: 0.3;
}

.product__bookmark {
  width: 135px;
  padding: 7px 0;
  color: #32425c;
  background-color: transparent;
  border: 3px solid #63a63e;
}

.product__bookmark:hover {
  border-color: #32425c;
  background-color: #ffffff;
}

.product__bookmark:active {
  opacity: 0.3;
}

.product__name {
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
  font-size: 18px;
  line-height: 20px;
  color: #000000;
  font-weight: 700;
}

.product__price-wrapper {
  position: relative;
  margin-top: auto;
  padding-top: 40px;
}

.product__price-old {
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 400;
  position: absolute;
  top: 13px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  font-size: 17px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: line-through;
  font-weight: 700;
  color: #999999;
}

.product__price {
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 400;
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 10px 0;
  max-width: 134px;
  font-size: 17px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  color: #ffffff;
  background-color: #ee3643;
  border-radius: 3px;
}

.product__price::before {
  position: absolute;
  top: 0;
  right: -26px;
  content: "";
  border: 19px solid #ffffff;
  border-left-color: #ee3643;
}

.brand {
  display: flex;
  width: 220px;
  height: 145px;
  margin: 0 10px 20px;
  border: 1px solid #eaeaea;
}

.brand:hover {
  box-shadow: 0 10px 25px 0 rgba(41, 52, 73, 0.5);
  transition: all 0.1s;
}

.brand:active {
  box-shadow: 0 4px 10px 0 rgba(41, 52, 73, 0.5);
  transition: all 0.1s;
}

.brand:active .brand__logo {
  opacity: 0.3;
}

.brand__logo {
  margin: auto;
  pointer-events: none;
}

.popular-brands {
  margin-bottom: 65px;
}

.popular-brands__heading {
  margin-bottom: 20px;
}

.popular-brands__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  padding: 0;
  list-style: none;
}

.services {
  padding: 65px 0 0;
  margin-bottom: 78px;
  background-color: #f4f7f9;
}

.services__title {
  margin: 0 0 27px 0;
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: 400;
}

.services__description {
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 400;
  margin: 0 0 22px 0;
  max-width: 400px;
  font-size: 13px;
  line-height: 24px;
  font-weight: 400;
}

.services__wrapper {
  display: flex;
  align-items: stretch;
}

.services__controls {
  position: relative;
  width: 240px;
  padding: 47px 0 102px;
}

.services__controls::before {
  position: absolute;
  top: 0;
  right: 0;
  width: 10px;
  height: 100%;
  content: "";
  background: url("../img/services-shadow.png") no-repeat 0 0;
}

.services__btn {
  display: block;
  width: 100%;
  padding: 15px 23px;
  font-size: 21px;
  line-height: 30px;
  font-weight: 700;
  text-align: left;
  color: #ffffff;
  background-color: #32425c;
  border: none;
  border-bottom: 1px solid #4d637b;
  cursor: pointer;
}

.services__btn:hover {
  background-color: #293449;
}

.services__btn:active {
  color: #32425c;
  background-color: #ffffff;
}

.services__btn--active {
  color: #32425c;
  background-color: #ffffff;
  border-color: #ffffff;
  cursor: auto;
}

.services__btn--active:hover {
  background-color: #ffffff;
}

.services__btn--active:active {
  color: #32425c;
  background-color: #ffffff;
}

.services__list {
  position: relative;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  width: 700px;
  height: auto;
}

.services__item {
  display: none;
  width: 100%;
  height: auto;
  padding: 45px 80px;
}

.services__item--active {
  display: block;
}

.services__item--delivery {
  background: url("../img/service-1.png") no-repeat 231px 70px;
}

.services__item--warranty {
  background: url("../img/service-2.png") no-repeat 310px 48px;
}

.services__item--credit {
  background: url("../img/service-3.png") no-repeat 235px 46px;
}

.services__service-title {
  max-width: 350px;
  margin: 0 0 24px 0;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-transform: uppercase;
  color: #32425c;
}

.services__service-description {
  font-family: "PTSans", "Arial", sans-serif;
  font-weight: 400;
  margin: 0 0 27px 0;
  max-width: 300px;
  font-size: 13px;
  line-height: 24px;
}

.services__button {
  width: 195px;
}

.wrapper-row {
  display: flex;
  justify-content: space-between;
}

.about-us {
  margin-bottom: 86px;
  max-width: 540px;
}

.about-us__title {
  margin: 0 0 26px 0;
  font-size: 30px;
  line-height: 30px;
  font-weight: 400;
